/* Kalkulator */
.kalkulator {
  margin-bottom: 3rem;
}

.input-container {
  position: relative;
  height: 15rem;
  width: 15rem;
  margin: 0.5rem;
}

.input-container input {
  position: absolute;
  height: 100%;
  width: 100%;
  margin: 0;
  cursor: pointer;
  z-index: 2;
  opacity: 0;
}

.input-container .radio-tile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  border-radius: 8px;
  transition: all 300ms ease;
}

.input-container label {
  color: black;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

input:checked + .radio-tile svg circle,
input:checked + .radio-tile label {
  fill: yellow;
}

.btn-custom {
  background-color: #019973 !important;
  color: white !important;
  border: none !important;
  align-content: center !important;
  transition: background-color 0.3s, color 0.3s !important;
}

.btn-custom:hover {
  background-color: #017a5c !important;
  color: #e0f7f4 !important;
}

.form-control {
  border: 1px solid #666666;
}

input:checked + .radio-tile2 {
  background-color: #019973;
  box-shadow: 0 0 12px #019973;
  transform: scale(1.1);
}

input:hover + .radio-tile2 {
  box-shadow: 0 0 12px #019973;
}

input:checked + .radio-tile2 ion-icon,
input:checked + .radio-tile2 label {
  color: white !important;
  fill: #019973;
}
