}

.arikel-pages-desc {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  font-weight: 500;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 14px;
  line-height: 1.5em;
  color: black;
  position: absolute;
  top: 8px;
  left: 8px;
  border-radius: 5px;
  padding: 5px 10px 5px;
  background-color: #ffcd39;
}

