/*
  LOADER
*/

#loader {
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: opacity 0.75s, visibility 0.75s;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  z-index: 9999;
}

#loader p {
  margin-top: 20px;
  color: white;
  font-size: 25px;
  font-weight: 500;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}
  
.ontop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  border: none; 
  outline: none; 
  background-color: #017a58; 
  color: white;
  cursor: pointer;
  padding: 7px;
  border-radius: 10px;
  font-size: 18px;
}

.ontop:hover {
  background-color: #555;
}
